.App-Full-box{width:100%;background:url(/static/media/ram.3fd1fd76.gif);height:auto;font-family:'Montserrat', sans-serif;background-position:center;background-size:cover;background-repeat:no-repeat}.App-Full-box #time{font-size:20px;color:#ff0;-webkit-text-stroke:0.5px black}.App-Full-box .twitter-icon{background-color:#06f;color:white;text-decoration:none;border:4px;padding:2px 3px;font-size:13px;font-family:'Chivo', sans-serif}.App-Full-box #demo{color:black;font-size:20px}.App-Full-box .share-buttons{display:flex;justify-content:space-around;width:13%}.App-Full-box .share-buttons1{margin-top:21%;font-size:25px}@media (max-width: 768px){.App-Full-box{width:auto;height:auto;position:absolute;left:0;right:0}.App-Full-box .share-buttons{width:70px;display:flex;justify-content:space-around}.App-Full-box .share-buttons1{margin-top:65%}.App-Full-box .icon{margin-left:10px}.App-Full-box body{background:linear-gradient(105deg, green, #128812)}}

